import * as yup from 'yup';

const CAMPO_REQUERIDO = '  obrigatrio';

export default yup.object().shape({
  tipoCategoria: yup.string()
    .required('Tipo' + CAMPO_REQUERIDO),
  codigo: yup.string()
    .max(10, 'O campo cdigo tem que ter 10 caracteres')
    .required('Cdigo' + CAMPO_REQUERIDO),
  descricao: yup.string()
    .max(60, 'A descrio deve ter no mximo 60 caracteres')
    .required('Descrio' + CAMPO_REQUERIDO),
})