import * as yup from 'yup'

export default yup.object().shape({
  email: yup.string()
    .email('Informe um e-mail vlido'),
  telefone: yup.string()
    .min(13, 'Telefone deve ter no mnimo 13 caracteres')
    .max(14, 'Telefone deve ter no mximo 14 caracteres'),
  senha: yup.string()
    .min(4, 'A senha deve ter pelo menos 4 caracteres')
    .max(12, 'A senha deve ter no mximo 12 caracteres'),
})