import * as yup from 'yup';
const CAMPO_REQUERIDO = '  um campo obrigatrio';

export default yup.object().shape({
  gateway: yup.object().shape({
    id: yup.number()
    .typeError('Gateway deve ser um nmero')
    .required('Gateway' + CAMPO_REQUERIDO)
  }),
  data: yup.date()
    .typeError('Entre com uma data vlida')
    .required('Data Cotao' + CAMPO_REQUERIDO),  
  debito: yup.string()
    .required('Dbito' + CAMPO_REQUERIDO),
  creditoAvista: yup.string()
    .required('Crdito Avista' + CAMPO_REQUERIDO),
  creditoParcelado: yup.string()
    .required('Crdito Parcelado' + CAMPO_REQUERIDO),
  creditoAntecipacao: yup.string()
    .required('Crdito Antecipado' + CAMPO_REQUERIDO),
  boleto: yup.string()
    .required('Boleto' + CAMPO_REQUERIDO),
  taxaAdministrativa: yup.string()
    .required('Taxa Administrativa' + CAMPO_REQUERIDO),
})