import * as yup from 'yup';
const CAMPO_REQUERIDO = '  obrigatrio';

export default yup.object().shape({
  conta: yup.object().shape({
    id: yup.number()
    .typeError('Conta deve ser um nmero')
    .required('Conta' + CAMPO_REQUERIDO)
  }),  
  nome: yup.string()
    .min(3, 'Nome deve ter no mnimo 3 caracteres')
    .max(50, 'Nome deve ter no mximo 50 caracteres')  
    .required('Nome' + CAMPO_REQUERIDO),
  tipoContaCaixa: yup.string()
    .required('Tipo de Conta' + CAMPO_REQUERIDO),
  mes: yup.number()
    .typeError('Ms deve ser um nmero')
    .min(1, 'Entre com um ms vlido')
    .max(12, 'Entre com um ms vlido')
    .required('Ms' + CAMPO_REQUERIDO),
  ano: yup.number()
    .typeError('Ano deve ser um nmero')
    .min(2020, 'Entre com um ano vlido')
    .required('Ano' + CAMPO_REQUERIDO),
})