import * as yup from 'yup';
const CAMPO_REQUERIDO = '  obrigatrio';

export default yup.object().shape({
  codigo: yup.string()
    .max(4, 'O cdigo deve ter no mximo 4 caracteres')
    .required('Cdigo' + CAMPO_REQUERIDO),
  nome: yup.string()
    .max(60, 'O nome deve ter no mximo 60 caracteres')
    .required('Nome' + CAMPO_REQUERIDO),
})