import * as yup from 'yup';
const CAMPO_REQUERIDO = '  obrigatrio';

export default yup.object().shape({
  tipoVeiculo: yup.string()
    .required('Tipo de Veculo' + CAMPO_REQUERIDO),
  descricao: yup.string()
    .min(3, 'Descrio deve ter no mnimo 3 caracteres')
    .max(45, 'Descrio deve ter no mximo 45 caracteres')
    .required('Descrio' + CAMPO_REQUERIDO),
  validadeInicio: yup.date()
    .typeError('Entre com uma data vlida')
    .required('Incio da Validade' + CAMPO_REQUERIDO),
  validadeFim: yup.date()
    .typeError('Entre com uma data vlida'),
  tarifaKm: yup.string()
    .required('KM Bsico' + CAMPO_REQUERIDO),
  tarifaValor: yup.string()
    .required('Valor KM Bsico' + CAMPO_REQUERIDO),
  ativo: yup.boolean()
    .required('Ativo' + CAMPO_REQUERIDO),    
  padrao: yup.boolean()
    .required('Padro' + CAMPO_REQUERIDO),     
})