import * as yup from 'yup';
const CAMPO_REQUERIDO = '  obrigatrio';

export default yup.object().shape({
  codigo: yup.string()
    .max(8, 'Cdigo deve ter no mximo 8 caracteres')
    .required('Campo cdigo' + CAMPO_REQUERIDO),
  email: yup.string()
    .email('Informe um e-mail vlido')
    .required('Campo e-mail' + CAMPO_REQUERIDO),
  senha: yup.string()
    .matches(/^\d+$/, 'Apenas nmero  permitido')
    .min(4, 'Senha deve ter pelo menos 4 caracteres')
    .max(6, 'Senha deve ter no mximo 6 caracteres')
    .required('Campo senha' + CAMPO_REQUERIDO),
  confirmeSenha: yup.string()
    .matches(/^\d+$/, 'Apenas nmero  permitido')
    .min(4, 'Senha deve ter pelo menos 4 caracteres')
    .max(6, 'Senha deve ter no mximo 6 caracteres')
    .oneOf([yup.ref('senha'), null], 'As senhas so diferentes') 
})