import * as yup from 'yup';
const CAMPO_REQUERIDO = '  obrigatrio';

export default yup.object().shape({
  banco: yup.object().shape({
    id: yup.number()
    .typeError('Banco deve ser um nmero')
    .required('Banco' + CAMPO_REQUERIDO)
  }),
  codigo: yup.string()
    .min(3, 'Cdigo deve ter no mnimo 3 caracteres')
    .max(15, 'Cdigo deve ter no mximo 15 caracteres')
    .required('Cdigo' + CAMPO_REQUERIDO),
  nome: yup.string()
    .min(3, 'Nome deve ter no mnimo 3 caracteres')
    .max(40, 'Nome deve ter no mximo 40 caracteres')
    .required('Nome' + CAMPO_REQUERIDO),
})