import * as yup from 'yup';
const CAMPO_REQUERIDO = '  obrigatrio';

export default yup.object().shape({
  pessoa: yup.object().shape({
    id: yup.number()
    .typeError('Membro deve ser um nmero')
    .required('Membro' + CAMPO_REQUERIDO)
  }), 
  registro: yup.string()
    .max(15, 'Registro deve ter no mximo 15 caracteres')
    .required('Registro' + CAMPO_REQUERIDO),
  validade: yup.date()
    .typeError('Entre com uma data vlida')
    .required('Validade' + CAMPO_REQUERIDO),    
  categoria: yup.string()
    .max(5, 'Categoria deve ter no mximo 5 caracteres')
    .required('Categoria' + CAMPO_REQUERIDO),
  localExpedicao: yup.string()
    .max(60, 'Local Expedio deve ter no mximo 60 caracteres')
    .required('Local Expedio' + CAMPO_REQUERIDO),
  dataEmissao: yup.date()
    .typeError('Entre com uma data vlida')
    .required('Data de Emisso' + CAMPO_REQUERIDO),
})