import * as yup from 'yup';
const CAMPO_REQUERIDO = '  obrigatrio';

export default yup.object().shape({
  chkNome: yup.string()
    .required('Nome no carto' + CAMPO_REQUERIDO),
  chkEmail: yup.string()
    .email('Informe um e-mail vlido'),
  chkNrCartao: yup.string()
    .nullable()
    .trim()
    .min(10, 'Nmero do carto deve ter 10 caracteres')
    .required('Nmero do carto' + CAMPO_REQUERIDO),
  chkCodSeguranca: yup.string()
    .nullable()    
    .trim()
    .min(3, 'CV deve ter no mnimo 3 caracteres')
    .max(4, 'CV deve ter no mximo 4 caracteres')
    .required('CV' + CAMPO_REQUERIDO),
  chkMes: yup.number()
    .typeError('Ms' + CAMPO_REQUERIDO)
    .required('Ms' + CAMPO_REQUERIDO)
    .min(1,'Ms invlido')
    .max(12, 'Ms invlido'),
  chkAno: yup.number()
    .typeError('Ano' + CAMPO_REQUERIDO)
    .required('Ano' + CAMPO_REQUERIDO)
    .min(2020, 'Ano invlido')
    .max(2030, 'Ano invlido'),
})