import * as yup from 'yup';
const CAMPO_REQUERIDO = '  obrigatrio';

export default yup.object().shape({
  pessoa: yup.object().shape({
    id: yup.number()
    .typeError('Membro deve ser um nmero')
    .required('Membro' + CAMPO_REQUERIDO)
  }),
  agencia: yup.object().shape({
    id: yup.number()
    .typeError('Agncia deve ser um nmero')
    .required('Agncia' + CAMPO_REQUERIDO)
  }),
  tipoConta: yup.string()
    .required('Tipo de Conta' + CAMPO_REQUERIDO),
  codigo: yup.string()
    .max(9, 'Cdigo deve ter no mximo 9 caracteres')
    .required('Cdigo' + CAMPO_REQUERIDO),
})